      });
    },
    getWebsocketMsg(sourDate) {
      let _this = this;
      let sour = sourDate.filter(item => {
        return item.indicateName === "中国地图" && item.group === "中国地图";
      });
      if (sour.length > 0) {
        sour.forEach(item => {
          _this.updateLine(item);
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    toRouter(name) {
      let target = name.match("东莞")
        ? "东莞"
        : name.match("深圳")
        ? "深圳"
        : "上海";
      if (!this.mapOnError.includes(target)) {
        this.$router.replace({
          name: "dailyMainJifang",
          params: { type: target }
        });
      } else {
        this.$router.push({
          name: "dailyMainJiGui",
          params: { type: target }
        });
      }
    },
    updateLine(sourDate, refreshJiFang = true) {
      if (this.sourDate === null) {
        this.sourDate = [];
      }
      sourDate.from.x = sourDate.from.x2;
      sourDate.from.y = sourDate.from.y2;
      sourDate.to.x = sourDate.to.x2;
      sourDate.to.y = sourDate.to.y2;
      let base = JSON.parse(JSON.stringify(this.sourDate));
      let isAddLine = true;
      for (let keys in base) {
        let item = base[keys];
        if (
          item.from.roomName === sourDate.from.roomName &&
          item.to.address === sourDate.to.address
        ) {
          this.sourDate[keys] = sourDate;
          isAddLine = false;
          break;
        }
      }
      if (isAddLine) {
        this.sourDate.push(sourDate);
      }
      let _this = this;
      this.initTimer && clearTimeout(this.initTimer);
      this.initTimer = setTimeout(() => {
        _this.initChart(refreshJiFang);
      }, 1000);
    },
    getCharsDate(sourDate) {
      let sour = sourDate || [];
      this.sourDate = sour.filter(item => {
        return item.indicateName === "中国地图" && item.group === "中国地图";
      });
      this.initChart();
    },
    initChart(refreshJiFang = true) {
      let jifangPoint = [];
      let lins = [];
      let showLine = [];
      let hasError = [];
      let nodesLine = [];
      let nameOnly = [];
      if (this.sourDate && this.sourDate.length > 0) {
        this.sourDate.forEach((element, index) => {
          this.jifangPointDate[element.from.roomName] = [
            element.from.x,
            element.from.y
          ];
          let nodeName = element.to.name;
          if (nameOnly.includes(element.to.name)) {
            nodeName = nodeName + index;
          } else {
            nameOnly.push(nodeName);
          }
          nodesLine.push({
            name: nodeName,
            draggable: false,
            value: [element.to.x, element.to.y],
            itemStyle: {
              color: "#f6ff54"
            }
          });
          if (element.value === 1) {
            hasError.push(element.from.roomName.replace("市", ""));
          }
          let theLineColor = element.value === 1 ? "red" : "#62eeff";
          let showLineItem = {
            type: "lines",
            zlevel: 1,
            coordinateSystem: "cartesian2d",
            effect: {
              show: element.value === 0,
              period: 4, // 箭头指向速度，值越小速度越快
              trailLength: 0, // 特效尾迹长度[0,1]值越大，尾迹越长重
              symbol: "image://" + this.lightPointBlue,
              symbolSize: 120
            },
            // effect: false,
            lineStyle: {
              normal: {
                curveness: element.from.x > element.to.x ? -0.2 : 0.1,
                type: "solid",
                opacity: 1,
                color: theLineColor,
                width: 8
              }
            },
            data: [
              {
                coords: [
                  [element.from.x, element.from.y + 5],
                  [element.to.x, element.to.y]
                ]
              }
            ]
          };
          showLine.push(showLineItem);
        });
      }
      // this.mapOnError = hasError
      if (refreshJiFang) {
        if (this.jifangPointDate) {
          for (let jifangKey in this.jifangPointDate) {
            let jifangPointItem = {
              name: jifangKey,
              symbol: "image://" + this.jifang,
              symbolSize: 200,
              draggable: false,
              fixed: true,
              // focusNodeAdjacency: true,
              value: this.jifangPointDate[jifangKey],
              label: {
                // 文字2
                normal: {
                  position: "bottom",
                  show: false,
                  distance: 5,
                  textStyle: {
                    fontSize: 40,
                    color: "#fff",
                    fontWeight: "500"
                  }
                }
              }
            };
            jifangPoint.push(jifangPointItem);
          }
        }
        for (let nodeKey of nodesLine) {
          let jifangPointItem = {
            name: nodeKey.name,
            symbol: "circle",
            symbolSize: 15,
            draggable: false,
            fixed: true,
            itemStyle: {
              normal: {
                color: "#62eeff"
              }
            },
            // focusNodeAdjacency: true,
            value: nodeKey.value,
            label: {
              // 文字2
              normal: {
                position: "bottom",
                show: false,
                distance: 5,
                textStyle: {
                  fontSize: 40,
                  color: "#fff",
                  fontWeight: "500"
                }
              }
            }
          };
          jifangPoint.push(jifangPointItem);
        }
        this.historyDate_Point = JSON.parse(JSON.stringify(jifangPoint));
        this.historyDate_Line = JSON.parse(JSON.stringify(lins));
      } else {
        jifangPoint = this.historyDate_Point;
        lins = this.historyDate_Line;
      }

      let graphs = [
        {
          type: "graph",
          coordinateSystem: "cartesian2d",
          legendHoverLink: false,
          hoverAnimation: true,
          nodeScaleRatio: false,
          roam: false,
          lineStyle: {
            // 去掉自带的那条线，不要删
            normal: {
              width: 0,
              shadowColor: "none",
              color: "transparent"
            }
          },
          symbolSize: 0,
          data: jifangPoint
        }
      ];

      let marksData = [
        {
          name: "1",
          value: [0, 0]
        },
        {
          name: "2",
          value: [100, 0]
        },
        {
          name: "3",
          value: [100, 100]
        },
        {
          name: "4",
          value: [0, 100]
        },
        {
          name: "点1",
          value: [50, 50]
        }
      ];

      let option = {
        tooltip: {
          formatter: "{b}",
          textStyle: {
            fontSize: 50
          }
        },
        grid: {
          left: 50,
          right: 50,
          bottom: "4%",
          top: "6%",
          containLabel: true
        },
        xAxis: {
          scale: true,
          axisLine: false,
          axisLabel: false,
          splitLine: false
        },
        yAxis: {
          scale: true,
          axisLine: false,
          axisLabel: false,
          splitLine: false
        },
        series: [
          {
            type: "scatter",
            data: marksData,
            itemStyle: false,
            symbolSize: 0,
            draggable: false,
            fixed: true,
            label: {
              normal: {
                show: false
              }
            },
            tooltip: {
              show: false
            }
          },
          ...graphs,
          ...lins,
          ...showLine
        ]
      };
      if (this.myChart !== null) {
        this.myChart.setOption(option);
      }
    }
  }
};
</script>
